import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home.vue'

Vue.use(Router)

export default new Router({
  mode: "history",
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }
  ]
})



